import React from 'react';
import Time from '../../Elements/Time/Time';
import Network from '../../Elements/Network/Network';
import Author from '../../Elements/Author/Author';
import Seprator from '../../Elements/Seprator/Seprator';
const AuthorDetails = ({ Post, CardStyle, inView }) => {
  const { author, networkId } = Post
  const username = author.username ? author.username.length > 0 ? `@${author.username}` : `` : '';
  const postAuthor = (CardStyle.author.status && !author.isInstaUser) ? true : false;
  const authorSeprator = (CardStyle.author.status && CardStyle.timeStatus && !author.isInstaUser) ? true : false;
  const networkIcon = (networkId !== null && networkId !== '') ? true : false
  return <div className="tb_hc_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    {postAuthor ? <Author network={Post.network} author={author} authorClass={'tb_hc_author_profile'} size={46} inView={inView} /> : ''}
    <div className="tb_hc_author_info">
      {postAuthor ? <div className={`tb_hc_authorname tb-cTBfont-${CardStyle.author.variant}`}>{author.name}</div> : ''}
      {<div className="tb_hc_post_info">
        {postAuthor ? <div className="tb_hc_username">{username}</div> : ''}
        {authorSeprator ? <Seprator sepClass={`tb_hc_seprator`} /> : ''}
        {CardStyle.timeStatus ? 
        <Time createdAt={Post.createdAt} timeClass={`tb_hc_time tb-cTBfont-${CardStyle.author.variant}`} /> : ''}
      </div>}
    </div>
    {networkIcon ?
      <div className="tb_hc_social_">
        <Network networkClass={'tb_hc_social_ico'} networkId={networkId} iconStyle={CardStyle.icon} />
      </div>
      : null}
  </div>
}
export default AuthorDetails